*,
*::after,
*::before {
	box-sizing: border-box;
}

html {
	box-sizing: border-box;
}

html,
body {
	margin: 0;
	padding: 0;
}

body {
	width: 100%;
	height: 100%;
	font-style: normal;
	font: 100 16px/1.15 var(--fontMain);
	color: var(--colorSecondary);
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

a {
	color: #000000;
	text-decoration: none;
}

img,
video {
	display: block;
	max-width: 100%;
	height: auto;
}

textarea {
	resize: none;
}

/* chrome autofill background removal
Если на проекте у инпутов используются разные цвета фонов\текста -
удалите это из глобала и используйте локально с нужными цветами
rgba не подойдет, сконвертируйте цвет в hex без прозрачности
если в стилях уже используется box-shadow есть другое решение -
задать к списку транзишенов `background-color 10000000s ease-out` */
input:-webkit-autofill {
	box-shadow: inset 0 0 0 1000px #ffffff;
	-webkit-text-fill-color: #000000;
}

/* firefox placeholder \ invalid fix + ios bdrs */

input,
textarea {
	border-radius: 0;
}

input::placeholder,
textarea::placeholder {
	opacity: 1;
}

input:invalid,
textarea:invalid {
	box-shadow: none;
}

select {
	border-radius: 0;
}

/* ie11 X removal */

input,
input::-ms-clear,
input::-ms-reveal {
	display: none;
}

/* chrome search X removal */

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	appearance: none;
}

/* input[number] arrows removal */

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	margin: 0;
	appearance: none;
}

input[type="number"] {
	appearance: textfield;
}

/* ios button \ inputs reset */

select,
textarea,
input:matches([type="email"], [type="number"], [type="password"], [type="search"], [type="tel"], [type="text"], [type="url"]) {
	appearance: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
	appearance: none;
}

/* Scrollbar */

body::-webkit-scrollbar {
	width: 8px;
	transition: var(--transition);
}

body::-webkit-scrollbar-track {
	box-shadow: inset 0 0 1.5px;
}

body::-webkit-scrollbar-thumb {
	border-radius: 5px;
	box-shadow: inset 0 0 30px var(--colorMain);
}

body::-webkit-scrollbar:hover {
	background-color: var(--colorGray);
}

body:hover {
	scrollbar-color: #ffffff;
}

/* Selection */

*::-moz-selection {
	background-color: var(--colorMain);
	color: #ffffff;
	text-shadow: -1px -1px 0 #000000, 1px -1px 0 #000000, -1px 1px 0 #000000,
		1px 1px 0 #000000;
}

*::selection {
	background-color: var(--colorMain);
	color: #ffffff;
	text-shadow: -1px -1px 0 #000000, 1px -1px 0 #000000, -1px 1px 0 #000000,
		1px 1px 0 #000000;
}

/* Focus */

.js-focus-visible :focus:not(.focus-visible) {
	outline: none;
}

.js-focus-visible .focus-visible {
	outline: none;
	/*outline: 2px solid rgba(59, 153, 252, 0.7);*/
	/*outline-offset: 2px;*/
}

/* Img */

img {
	position: relative;
	display: block;
	width: 100%;
	height: auto;
	font-family: sans-serif;
	font-weight: 300;
	line-height: 2;
	text-align: center;
}

img::before {
	content: "Изображение не загрузилось 😔";
	display: block;
	margin-bottom: 10px;
	font-size: 12px;
}

button:active {
	transform: translateY(1.5px);
}
